  notificationMethods,
  authComputed,
} from "@/state/helpers";
import { mapState } from "vuex";

import appConfig from "@/app.config";
import { required } from "vuelidate/lib/validators";

/**
 * Login component
 */
export default {
  page: {
    title: "로그인",
    meta: [
      {
        name: "description",
        content: appConfig.description,
      },
    ],
  },
  components: {
    Layout,
  },
  data() {
    return {
      loginId: "admin",
      password: "passwd",
      checkbox_remember_me: false,
      submitted: false,
      authError: null,
      tryingToLogIn: false,
      isAuthError: false,
      isLoading: true,
    };
  },
  validations: {
    loginId: {
      required
    },
    password: {
      required,
    },
  },
  computed: {
    ...authComputed,
    ...mapState("authfack", ["status"]),
    notification() {
      return this.$store ? this.$store.state.notification : null;
    },
    validateForm () {
      return !this.$v.$invalid && this.loginId !== '' && this.password !== ''
    },
  },
  methods: {
    // ...authMethods,
    // ...authFackMethods,
    ...notificationMethods,
       checkLogin () {
      // If user is already logged in notify
      if (this.isUserLoggedIn) {
        // Close animation if passed as payload
        // this.$vs.loading.close()
        this.$auth.cleanseLoginInfo()
      }
    },
    loginJWT () {
      //TODO Loading
      this.isLoading = true
      this.checkLogin()
      this.submitted = true;
      // stop here if form is invalid
      this.$v.$touch();

      const payload = {
        checkbox_remember_me: this.checkbox_remember_me,
        userDetails: {
          loginId: this.loginId,
          password: this.password
        }
      }

      this.$store.dispatch('auth/loginJWT', payload)
      .then(() => { 
        this.isLoading = false
      })
      .catch(error => {
        this.authError = error ? error.message : "";
        this.isAuthError = true;
        this.isLoading = false
      })
    },
  },
  mounted() {},
};
</script>

